@import url(https://unpkg.com/@tailwindcss/typography@0.2.x/dist/typography.min.css);
@import url(https://unpkg.com/tailwindcss@^1.0/dist/tailwind.min.css);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100;400&family=Sora&display=swap);

:root {
  --primary: #4734E1;
  --primaryAccent: #415DDA;
  --secondary: #8E27EA;
  --secondaryAccent: #8253DF;
  --tertiary: #EFEFEF;
}

html {
  overflow-y: scroll;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Sora', sans-serif;
  font-size: 0.95rem;
}
.main-gradient{
  background-image: linear-gradient(90deg, #4734E1, #8E27EA);
  background-image: linear-gradient(90deg, var(--primary), var(--secondary));
}


h1 {
  font-family: 'IBM Plex Sans', sans-serif ;
}

h2{
  font-size: 1.25rem;
}


.main-gradient{
  background-image: linear-gradient(90deg, #4734E1, #8E27EA);
  background-image: linear-gradient(90deg, var(--primary), var(--secondary));
}

.bg-tertiary{
  background-color: #EFEFEF;
  background-color: var(--tertiary);
}

.pills input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.pills label {
    display: inline-block;
    background-color: #EFEFEF;
    background-color: var(--tertiary);
    font-size: 0.8rem;
}

.pills label:hover {
  background-image: linear-gradient(90deg, #415DDA, #8253DF);
  background-image: linear-gradient(90deg, var(--primaryAccent), var(--secondaryAccent));
  color: white;
}

.pills input[type="radio"]:focus + label {
    background-color: #EFEFEF;
    background-color: var(--tertiary);
}

.pills input[type="radio"]:checked + label {
  background-image: linear-gradient(90deg, #4734E1, #8E27EA);
  background-image: linear-gradient(90deg, var(--primary), var(--secondary));
  color: white;
}

.container {
  width: 100%;
  padding: 0 5vw;
}

.check-input input[type="radio"] {
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  outline: 0;
  background: #EFEFEF;
  background: var(--tertiary);
  height: 20px;
  width: 20px;
  margin-top: 5px;
  border: 1px solid white;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.check-input input[type="radio"]:checked {
  background-image: linear-gradient(90deg, #4734E1, #8E27EA);
  background-image: linear-gradient(90deg, var(--primary), var(--secondary));
}

.check-input input[type="radio"]:after {
  content: '';
  position: relative;
  left: 40%;
  top: 20%;
  width: 20%;
  height: 60%;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  display: none;
}

.check-input input[type="radio"]:checked:after {
  display: block;
}

@-webkit-keyframes slide-down {
  0%{
    right: -20%;
    top: -50%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25%{
    top: -25%;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  50%{
    opacity: 0.5;
    top: 10%;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100%{
    right: -10%;
    top: -10%;
    opacity: 0;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

@keyframes slide-down {
  0%{
    right: -20%;
    top: -50%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25%{
    top: -25%;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  50%{
    opacity: 0.5;
    top: 10%;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100%{
    right: -10%;
    top: -10%;
    opacity: 0;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

@-webkit-keyframes slide-side {
  0%{
    left: -10%;
    bottom: -50%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100%{
    opacity: 0;
    left: 60%;
    bottom: -20%;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

@keyframes slide-side {
  0%{
    left: -10%;
    bottom: -50%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100%{
    opacity: 0;
    left: 60%;
    bottom: -20%;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

}

@-webkit-keyframes small-move {
  0% {
    opacity: 1;
    -webkit-transform: rotate(50deg);
            transform: rotate(50deg);
    bottom: 0;
    left: 30%;
  }
  100%{
    opacity: 0;
    -o-transform: (90deg);
    bottom: 20%;
    left: 25%;
  }
}

@keyframes small-move {
  0% {
    opacity: 1;
    -webkit-transform: rotate(50deg);
            transform: rotate(50deg);
    bottom: 0;
    left: 30%;
  }
  100%{
    opacity: 0;
    -o-transform: (90deg);
    bottom: 20%;
    left: 25%;
  }
}

@-webkit-keyframes mobile-top{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    top: -40%;
    right: -50%;
    opacity: 1;
  }
  100%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    top: -30%;
    right: -30%;
    opacity: 0;
  }
}

@keyframes mobile-top{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    top: -40%;
    right: -50%;
    opacity: 1;
  }
  100%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    top: -30%;
    right: -30%;
    opacity: 0;
  }
}

@-webkit-keyframes mobile-bottom{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom: -40%;
    left: -50%;
    opacity: 1;
  }
  100%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    bottom: -30%;
    left: -30%;
    opacity: 0;
  }
}

@keyframes mobile-bottom{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom: -40%;
    left: -50%;
    opacity: 1;
  }
  100%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    bottom: -30%;
    left: -30%;
    opacity: 0;
  }
}


.shape {
  z-index: -10;
}
.slide-down {
  -webkit-animation: slide-down 15s infinite alternate;
          animation: slide-down 15s infinite alternate;
}

.slide-side {
  -webkit-animation: slide-side 15s infinite alternate;
          animation: slide-side 15s infinite alternate;
}

.small-move {
  -webkit-animation: small-move 8s infinite alternate;
          animation: small-move 8s infinite alternate;
}

.mobile-top{
  -webkit-animation: mobile-top 8s infinite alternate;
          animation: mobile-top 8s infinite alternate;
}
.mobile-bottom{
  -webkit-animation: mobile-bottom 8s infinite alternate;
          animation: mobile-bottom 8s infinite alternate;
}
